export enum OrderFieldset {
  /** Returns `tickets`. */
  TICKETS = 'TICKETS',
  /**
   * Returns `archived`, `confirmed`, `created`, `firstName`, `fullName`,
   * `lastName`, `method`, `reservationId`, `snapshotId`,
   * `status`, `ticketsPdf`, `ticketsQuantity`, `totalPrice`.
   */
  DETAILS = 'DETAILS',
  /** Returns `checkoutForm`. */
  FORM = 'FORM',
  /** Returns `invoice`. */
  INVOICE = 'INVOICE',
}

/** @enumType */
